$(document).ready(function () {
  //Main-nav
  //On scroll functionality
  $(window).scroll( () => {
    var windowTop = $(window).scrollTop();
    windowTop > 100 ? $('.header').addClass('navShadow') : $('.header').removeClass('navShadow');
    windowTop > 100 ? $('.list-menu').css('top','100px') : $('.list-menu').css('top','160px');
  });
  
  //Toggle menu
  $('.menu-toggle').on('click', () => {
    $('.menu-toggle').toggleClass('closeMenu');
    $('.list-menu').toggleClass('showMenu');
    $('body').toggleClass('overflow-h');
    
    $('li').on('click', () => {
      $('.list-menu').removeClass('showMenu');
      $('.menu-toggle').removeClass('closeMenu');
    });
  });

  $('.testimonials-slider').slick({
    dots: true,
    arrows: false,
    infinite: true,
    speed: 300,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
  });

  $('.partners-list').slick({
    dots: false,
    arrows: false,
    infinite: true,
    speed: 300,
    slidesToShow: 5,
    slidesToScroll: 1,
    autoplay: false,
    responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 1,
            dots: true,
            autoplay: true,
            infinite: true,
            autoplaySpeed: 3000
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            dots: true,
            autoplay: true,
            infinite: true,
            autoplaySpeed: 3000
          }
        },
        {
          breakpoint: 580,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: true,
            autoplay: true,
            infinite: true,
            autoplaySpeed: 3000,
          }
        }
      ]
  });

  // tab
  $('.tab-menu a').on('click', function(e){
    e.preventDefault();
    $('.tab-menu li').removeClass('active-tab');
    $(this).parent('li').addClass('active-tab');
    
    var hrefdata =$(this).attr('href');
    $('.tab-data .data-content').removeClass('active-data');
    $('.tab-data .data-content#'+hrefdata+'').addClass('active-data');
    $('.active-data').prevAll('.data-content').addClass('prev').removeClass('next');
    $('.active-data').nextAll('.data-content').addClass('next').removeClass('prev');
  });

  var gallery = $('.tab-data-wrap .gallery-item').simpleLightbox();

  $('.tab-data-wrap').slick({
    dots: true,
    arrows: false,
    speed: 300,
    slidesToShow: 7,
    slidesToScroll: 2,
    // autoplay: true,
    // infinite: true,
    // autoplaySpeed: 3000,
    responsive: [
        {
          breakpoint: 1920,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 1441,
          settings: {
            slidesToShow: 5
          }
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 4
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 580,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        }
      ]
  });

   

});
